@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.index-view{display:flex;flex-direction:row;flex:left 1;flex-wrap:wrap;justify-content:space-around;align-content:space-evenly;padding-top:20px}
.card-container{box-shadow:-1px 3px 3px 2px rgba(0,0,0,.25);padding:20px}
header{background:#b2b5cc;background:linear-gradient(96deg, #b2b5cc 0%, #accee3 72%);text-align:left;padding:20px 10px;display:flex;justify-content:space-between;font-family:"Playfair Display",serif;font-size:25px;box-shadow:-1px 3px 3px 2px rgba(0,0,0,.25)}header a{color:#3f3a3a;transition:color .3s ease;text-decoration:none}header a:hover{color:#ccc}header ul{display:flex;margin:0;padding:0}header ul li{list-style-type:none;padding:0px 10px}
.footer-container{background:#b2b5cc;background:linear-gradient(96deg, #b2b5cc 0%, #accee3 72%);display:flex;flex-direction:row;flex:left 1;justify-content:space-between;position:fixed;left:0;bottom:0;width:100%;font-family:"Playfair Display",serif;color:#3f3a3a;transition:color .3s ease;box-shadow:3px 1px 3px 2px rgba(0,0,0,.25)}.footer-container .company-name,.footer-container .copyright{font-size:20px}.footer-container .copyright{margin-left:140px}.footer-container .advertise{font-size:15px}.footer-container .advertise a{text-decoration:none}@media screen and (max-width: 850px){.footer-container{width:375px;font-size:10px}.company-name{position:absolute;margin-top:15px}.copyright{margin-top:15px;margin-right:50px;position:absolute}.advertise{font-size:10px;margin-left:180px;width:150px}.advertise a{font-size:15px;text-decoration:none}}
.orderForm{margin:auto;position:relative;width:500px;margin-bottom:50px}.orderForm .contact-form{display:flex;flex-direction:column;width:300px;text-align:left}.orderForm .contact-form textarea{width:300px;height:150px}.orderForm .contact-form .submit{width:200px;margin-left:50px;font-size:20px;font-weight:800;border-radius:15px;background:#ace3d2;background:radial-gradient(circle, #ace3d2 0%, #418641 72%);font-family:"Playfair Display",serif}@media screen and (max-width: 850px){.orderForm{margin-left:30px;margin-bottom:100px}.submit{margin-top:20px}}
.App{text-align:center;font-family:"Playfair Display",serif}
